

#header {
    background-color: var(--primary-color);
    
    width: 100%;

    padding: max(1.8vh, 18px) 0;
}
#header .text-area{
    margin: 0 auto;

    width: fit-content;
}
.text-area h4{
    font-size: max(2vw, 2vh, 20px);

    padding-left: max(2.2vw, 2.2vh, 22px);
    margin-top: 0;
    margin-bottom: min(-0.5vw, -0.5vh, -5px);
}
.text-area h1{
    text-align: center;
    font-size: max(3.2vw, 3.2vh, 32px);

    margin-top: 0;
    margin-bottom: 0;
}


#content {
    background-attachment: fixed;
    background-image: linear-gradient(var(--primary-color), var(--background-color));
    background-size: 100vw 100vh;
    background-repeat: round;

    box-sizing: border-box;
    min-height: 100vh;

    padding-top: max(0.8vh, 8px);
}



.help-button {
    width: fit-content;

    float: left;
    display: inline;

    margin-left: 10px;
    margin-right: 0;
    margin-bottom: 1vh;
}

.help-button .button {
    width: 100%;
}
