#image-view {
    box-sizing: border-box;

    width: 100%;

    display: flex;
    flex-direction: column;
}

.back-button {
    width: fit-content;

    float: right;

    margin-left: auto;
    margin-right: 10px;
    margin-bottom: 1vh;
}

.back-button .button {
    width: 100%;
}

.back-button .button:hover {
    background-color: var(--hover-color);
}


#image-view .image-container {
    position: relative;
    width: 100%;
    height: min-content;

    margin: 1vh 0;
}


.image-container .main-frame {
    box-sizing: border-box;

    width: 100%;

    padding: 0 8px;
}


.image-container .switch-control {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.switch-control .switch-button {
    position: absolute;
    top: 0;

    width: max(4.6vw, 4.6vh, 46px);
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}
.switch-button svg {
    width: max(2vw, 2vh, 20px);
}
.switch-button.next {
    right: 0;
}
.switch-button.previous {
    left: 0;
}
.switch-button.next svg {
    transition: all 300ms ease-in-out;
}
.switch-button.previous svg {
    transition: all 300ms ease-in-out;
    transform-origin: center;
    transform: scale(-1, 1);
}
@media (hover: none) {
    .switch-button.next:hover svg {
        transform: translate(max(0.6vw, 0.6vh, 6px), 0);
    }
    .switch-button.previous:hover svg {
        transform: scale(-1, 1) translate(max(0.6vw, 0.6vh, 6px), 0);
    }
}
@media (hover: hover) {
    .switch-button.next:hover svg {
        transform: translate(max(0.6vw, 0.6vh, 6px), 0);
    }
    .switch-button.previous:hover svg {
        transform: scale(-1, 1) translate(max(0.6vw, 0.6vh, 6px), 0);
    }
}



#image-view .download-button {
    background-color: var(--primary-color);

    font-size: max(2.2vw, 2.2vh, 22px);

    padding: max(1.6vw, 1.6vh, 16px) 0;
}
    
#image-view .save-button {
    background-color: var(--primary-color);

    font-size: max(1.8vw, 1.8vh, 18px);

    padding: max(1.2vw, 1.2vh, 12px) 0;
    margin: 0.5vh 0;
}

@media (hover: none) {
    #image-view .download-button:active {
        background-color: var(--background-color);
    }
    
    #image-view .save-button:active {
        background-color: var(--background-color);
    }
}
@media (hover: hover) {
    #image-view .download-button:hover {
        background-color: var(--background-color);
    }
    
    #image-view .save-button:hover {
        background-color: var(--background-color);
    }
}


#image-view .edition-selection {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: row;

    margin-bottom: 2vh;
}

.edition-selection .edition-button {
    font-size: max(2.2vw, 2.2vh, 22px);

    width: 50%;

    padding: 2vh 1vw;
    margin: 0.7vh 1vw;
}

#image-view .customizations {
    background-color: transparent;

    width: 100%;
    margin: 1vh 0;

    display: flex;
    flex-direction: column;
    gap: 1vh;
}

.customizations .generate {
    width: 100%;

    padding: max(1.6vw, 1.6vh, 16px) 0;
    border-style: solid none solid none;
}

.customizations .config {
    margin: 1vh 2vw;

    display: grid;
    grid-template-columns: 25% auto;
}

.config label {
    grid-column: 1 / 1;

    font-size: max(1.7vw, 1.7vh, 17px);
    font-weight: 500;
}

.config .input {
    grid-column: 2 / 2;
}

.input p.text-input {
    font-size: max(1.3vw, 1.3vh, 13px);
    position: absolute;
    transform: translateX(calc(-100% - 6px));
}

.config .select-layout {
    margin-left: auto;
    margin-right: max(4vw, 4vh, 4  0px);

    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto;
    gap: max(0.6vw, 0.6vh, 6px) max(0.6vw, 0.6vh, 6px);
}

.select-layout .select-button {
    background-color: var(--secondary-color);
    width: max(4.6vw, 4.6vh, 46px);
    height: max(4.6vw, 4.6vh, 46px);
    padding: 0 0;
    border: 0;
    border-radius: max(0.7vw, 0.7vh, 7px);
}

.select-button.selected {
    background-color: var(--hover-color);
}

.select-button .icon {
    width: max(4.6vw, 4.6vh, 46px);
    height: max(4.6vw, 4.6vh, 46px);
}


.config .color-picker {
    width: 100%;
}