

#login-page {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#login-page h1 {
    text-align: center;
    margin: 0;
}

#login-page input[type="password"] {
    margin: 3vh 0;
}