

#images-area {
    width: 100%;
    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
}

#images-area a {
    box-sizing: border-box;

    width: 84%;
    max-width: 500px;
    min-height: 150px;

    padding: 2px 2px;
    margin: 0.6vh auto;
}

#images-area img {
    width: 100%;
    height: 100%;
}