body{
    --heading-size: max(2.4vw, 2.4vh, 24px);
    --default-font-size: max(1.6vw, 1.6vh, 16px);

    --primary-color: #ADACBA;
    --secondary-color: #CDB1A2;
    --background-color: #8B9DAE;
    --hover-color: #bbbbb0;

    font-family: Georgia, 'Times New Roman', Times, serif;

    margin: 0 0;
}


#root {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}


.backup {
    --main-color: #ffffff;
    --highlight-color: #fffff0;
    --secondary-color: #D0E6F8;
    --background-color: #fffffb;
}


h2 {
    font-size: var(--heading-size);

    margin-top: max(4vw, 4vh, 40px);
    margin-left: max(3vw, 3vh, 30px);
}


p {
    font-size: var(--default-font-size);
}


button {
    font-size: var(--default-font-size);
    color: black;

    background-color: var(--secondary-color);

    padding: max(0.8vw, 0.8vh, 8px) max(3vw, 3vh, 30px);

    border-width: 2px;
    border-color: black;
    border-radius: 0px;
}
@media (hover: none) {
    button:active {
        background-color: var(--hover-color);
    }
}
@media (hover: hover) {
    button:hover {
        background-color: var(--hover-color);
    }
}

select {
    background-color: var(--secondary-color);

    font-size: var(--default-font-size);
    text-align: center;

    box-sizing: border-box;

    min-width: 100%;
    max-width: fit-content;

    padding: max(0.5vw, 0.5vh, 5px) max(2vw, 2vh, 20px);
    border-radius: 12px;
}
@media (hover: none) {
    select:active {
        background-color: var(--hover-color);
    }
}
@media (hover: hover) {
    select:focus-within:hover {
        background-color: var(--hover-color);
    }
}

input[type="password"] {
    background-color: var(--secondary-color);

    font-size: max(3vw, 3vh, 30px);

    font-size: var(--default-font-size);
    text-align: left;

    box-sizing: border-box;

    width: 50vw;

    padding: 3px 7px;
}

textarea {
    background-color: var(--secondary-color);

    font-size: var(--default-font-size);
    text-align: left;

    box-sizing: border-box;

    width: 100%;
    min-height: max(6vw, 6vh, 60px);

    padding: 3px 7px;
}

input[type="range"] {
    background-color: transparent;
    width: 100%;
}
input[type="range"]::-moz-range-track {
    background: var(--secondary-color);
    height: max(1vw, 1vh, 10px);
    border-radius: 10px;
}
input[type="range"]::-moz-range-thumb {
    background: var(--primary-color);
    width: max(1.4vw, 1.4vh, 14px);
    height: max(1.4vw, 1.4vh, 14px);
    border-radius: 100%;
}